.App {
  text-align: center;
  min-height: 100vh;
}
.light-mode {
  padding: 15;
  background-color: #e0f2f1;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  height: 60px;
  vertical-align: middle;
  position: fixed;
  width: 100%;
  z-index: 1;
}

.dark-mode {
  padding: 15;
  background-color: #000;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  color: white;
  height: 60px;
  vertical-align: middle;
  position: fixed;
  width: 100%;
  z-index: 1;
}

.list {
}

.image {
  width: 200px;
  hwight: 200px;
  aspect-ratio: 1;
  transition: transform 0.2s; /* Animation */
  background-color: aqua;
  margin: 20px;
}

.image:hover {
  transform: scale(
    1.1
  ); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

@media (min-width: 320px) and (max-width: 480px) {
  .light-mode {
    padding: 10;
  }

  .dark-mode {
    padding: 10;
  }
}
